import { Component, OnInit } from '@angular/core';
import { AppURL } from 'src/app/app.url';
import { AuthURL } from 'src/app/auth/auth.url';
import { IAuthSidebarComponent } from './auth-sidebar.interface';
import { AuthenService } from '../../services/authen.service';
import { AlertService } from '../../services/alert.service';
import { Router } from '@angular/router';
import { AccountService, IAccount } from '../../services/account.service';

@Component({
  selector: 'app-auth-sidebar',
  templateUrl: './auth-sidebar.component.html',
  styleUrls: ['./auth-sidebar.component.scss']
})
export class AuthSidebarComponent implements OnInit,IAuthSidebarComponent {

  constructor(
    private authen:AuthenService,
    private alert:AlertService,
    private router:Router,
    private account:AccountService
  ) { 
    this.account.
    onGetEmployeeLogin(this.authen.getAuthentication())
    .then(employeeLogin => this.EmployeeLogin = employeeLogin)
    .catch(err => {
      this.alert.notify("Access Token ไม่ถูกต้อง");
      this.router.navigate(['/',AppURL.Login]);
    })
  }

  ngOnInit() {
  }
  EmployeeLogin:IAccount;
  AppURL = AppURL;
  AuthURL = AuthURL;
}
