import { Component, OnInit } from '@angular/core';
import { AppURL } from 'src/app/app.url';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ILoginComponent } from './login.interface';
import { stringify } from '@angular/core/src/render3/util';
import { AlertService } from 'src/app/shareds/services/alert.service';
import { AccountService } from 'src/app/shareds/services/account.service';
import { Router } from '@angular/router';
import { AuthURL } from 'src/app/auth/auth.url';
import { AuthenService } from 'src/app/shareds/services/authen.service';
declare const App:any;
@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit,ILoginComponent {
  

  constructor(
    private builder:FormBuilder,
    private alert:AlertService,
    private account:AccountService,
    private router:Router,
    private authen:AuthenService
  ) { 
    this.initialFormData();
  }
  
  ngOnInit() {
    App.initialLoadPageForgotPassword();
  }
  onSubmit(){
    if(this.form.invalid) return this.alert.something_wrong();
    return this.account
    .onLogin(this.form.value)
    .then(res => {
      this.alert.notify("เข้าสู่ระบบสำเร็จ");
      this.authen.setAuthentication(res.accessToken);
      
      this.router.navigate(['/',AppURL.Auth,AuthURL.Dashboard])
    })
    .catch(err => this.alert.notify(err.Message));
  }
  form:FormGroup;
  AppURL = AppURL;

  private initialFormData(){
    this.form = this.builder.group({
      email:['',[Validators.required,Validators.email]],
      password:['',Validators.required],
      remember:[true]
    })
  }
}
